import React, {useEffect, useState} from 'react'
import { Link, useNavigate } from 'react-router-dom'
import {
  CButton,
  CCard,
  CCardBody,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CImage,
  CFormSelect,
  CFormCheck,
  CInputGroup,
  CInputGroupText,
  CRow,
  CAlert,
  CCardHeader,
  CDatePicker,
  CFormTextarea
} from '@coreui/react-pro'
import axios from 'axios'
import CIcon from '@coreui/icons-react'
import { toast } from 'react-toastify'

const AddProduct = () => {
    const navigate = useNavigate();
    const [manuDesc, setManuDesc] = useState("")
    const [distItemNum, setDistItemNum] = useState("")
    const [name, setName] = useState("")
    const [manuItemNum, setManuItemNum] = useState("")
    const [productType, setProductType] = useState("")
    const [caseDime, setCaseDime] = useState("")
    const [servingSize, setServingSize] = useState("")
    const [shelfLifeManu, setShelfLifeManu] = useState("")
    const [storageMethod, setStorageMethod] = useState("")
    const [tips, setTips] = useState("")
    const [ingreList, setIngreList] = useState("")
    const [servicePerCont, setServicePerCont] = useState("")
    const [serviceSizeQty, setServiceSizeQty] = useState("")
    const [serviceSizeWt, setServiceSizeWt] = useState("")
    const [shelfLife, setshelfLife] = useState("")
    
    const [distributerId, setDistributerId] = useState("")
    const [manuMarkStmt, setManuMarkStmt] = useState("")
    const [caseCount, setCaseCount] = useState("")
    const [masterPack, setMasterPack] = useState("")
    const [grossCaseWeight, setGrossCaseWeight] = useState("")
    const [masterUnitSize, setMasterUnitSize] = useState("")
    const [gtinNum, setGtinNum] = useState("")

    const [calories, setCalories] = useState("")
    const [cholestrol, setCholestrol] = useState("")
    const [totalFat, setTotalFat] = useState("")
    const [saturatedFat, setSaturatedFat] = useState("")
    const [transFat, setTransFat] = useState("")
    const [sodium, setSodium] = useState("")
    const [totalCarboht, setTotalCarboht] = useState("")
    const [dietaryFiber, setDietaryFiber] = useState("")
    const [totalSugar, setTotalSugar] = useState("")
    const [addedSugar, setAddedSugar] = useState("")
    const [protein, setProtein] = useState("")
    const [vitD, setVitD] = useState("")
    const [calcuim, setCalcuim] = useState("")
    const [iron, setIron] = useState("")
    const [potassium, setPotassium] = useState("")

    const [cnLabel, setCnLabel] = useState("Yes")
    const [isActive, setIsActive] = useState(1)

    const [isSubmitting, setIsSubmitting] = useState(false);
    const [formData, setFormData] = useState([{ imageType: '', logo: '', logoFile: null, base64Image: '' }]);

    const [clients, setClients] = useState([])
    const [errors, setErrors] = useState({});

    const handleAddForm = () => {
      setFormData([...formData, { imageType: '', logo: '', logoFile: null, base64Image: '' }]);
    };
  
    const handleRemoveForm = (index) => {
      const updatedFormData = [...formData];
      updatedFormData.splice(index, 1);
      setFormData(updatedFormData);
    };
  
    const handleInputChange = async (index, event) => {
      const { name, value, files } = event.target;
      const updatedFormData = [...formData];
  
      if (files) {
        const file = files[0];
        updatedFormData[index]['logoFile'] = URL.createObjectURL(file);
  
        // Convert the image file to a base64 encoded string
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onloadend = () => {
          updatedFormData[index]['base64Image'] = reader.result;
          setFormData(updatedFormData);
        };
      } else {
        updatedFormData[index][name] = value;
        setFormData(updatedFormData);
      }
    };

  useEffect(() => {  
    axios.get('client/findAll/distributer', {headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }}).then(response => setClients(response.data.data))
    .catch(error => console.error('error fetching clients', error));
  }, [])

  const validateForm = () => {
    const errors = {};
    if (!manuItemNum.trim()) {
      errors.manuItemNum = 'First Name is required';
    }
    if (!manuDesc.trim()) {
      errors.manuDesc = 'Last Name is required';
    }
    if (!name.trim()) {
      errors.name = 'Email is required';
    }
    if (!manuMarkStmt.trim()) {
      errors.manuMarkStmt = 'username is required';
    }
    if (!distItemNum.trim()) {
      errors.distItemNum = 'Password is required';
    }
    if (!distributerId.trim()) {
      errors.distributerId = 'Work phone is required';
    }
    if (!productType.trim()) {
      errors.productType = 'Work address is required';
    }
    if (!caseCount.trim()) {
      errors.caseCount = 'Work address is required';
    }
    if (!masterPack.trim()) {
      errors.masterPack = 'State is required';
    }
    if (!grossCaseWeight.trim()) {
      errors.grossCaseWeight = 'City is required';
    }
    if (!masterUnitSize.trim()) {
      errors.masterUnitSize = 'Zip code is required';
    }
    if (!caseDime.trim()) {
      errors.caseDime = 'Zip code is required';
    }
    if (!servingSize.trim()) {
      errors.servingSize = 'Zip code is required';
    }
    if (!shelfLife.trim()) {
      errors.shelfLife = 'Zip code is required';
    }
    if (!shelfLifeManu.trim()) {
      errors.shelfLifeManu = 'Zip code is required';
    }
    if (!storageMethod.trim()) {
      errors.storageMethod = 'Zip code is required';
    }
    if (!ingreList.trim()) {
      errors.ingreList = 'Zip code is required';
    }
    if (!tips.trim()) {
      errors.tips = 'Zip code is required';
    }
    if (!servicePerCont.trim()) {
      errors.servicePerCont = 'Zip code is required';
    }
    if (!serviceSizeWt.trim()) {
      errors.serviceSizeWt = 'Zip code is required';
    }
    if (!serviceSizeQty.trim()) {
      errors.serviceSizeQty = 'Zip code is required';
    }
    if (!calories.trim()) {
      errors.calories = 'Zip code is required';
    }
    if (!cholestrol.trim()) {
      errors.cholestrol = 'Zip code is required';
    }
    if (!sodium.trim()) {
      errors.sodium = 'Zip code is required';
    }
    if (!totalFat.trim()) {
      errors.totalFat = 'Zip code is required';
    }
    if (!saturatedFat.trim()) {
      errors.saturatedFat = 'Zip code is required';
    }
    if (!transFat.trim()) {
      errors.transFat = 'Zip code is required';
    }
    if (!totalCarboht.trim()) {
      errors.totalCarboht = 'Zip code is required';
    }
    if (!dietaryFiber.trim()) {
      errors.dietaryFiber = 'Zip code is required';
    }
    if (!totalSugar.trim()) {
      errors.totalSugar = 'Zip code is required';
    }
    if (!addedSugar.trim()) {
      errors.addedSugar = 'Zip code is required';
    }
    if (!protein.trim()) {
      errors.protein = 'Zip code is required';
    }
    if (!vitD.trim()) {
      errors.vitD = 'Zip code is required';
    }
    if (!calcuim.trim()) {
      errors.calcuim = 'Zip code is required';
    }
    if (!iron.trim()) {
      errors.iron = 'Zip code is required';
    }
    if (!potassium.trim()) {
      errors.potassium = 'Zip code is required';
    }
    
    console.log(errors);
    // Add validations for other fields

    setErrors(errors);
    return Object.keys(errors).length === 0;
  };
 
    
    const addProductAction = async (e) => {
        e.preventDefault();
        if(validateForm()){
          setIsSubmitting(true)
          let payload = {
              manuDesc: manuDesc,
              name: name,
              distItemNum:distItemNum,
              manuItemNum:manuItemNum,
              manuMarkStmt:manuMarkStmt,
              distributerId: distributerId,
              productType: productType,
              caseDime: caseDime,
              servingSize: servingSize,
              shelfLifeManu: shelfLifeManu,
              storageMethod: storageMethod,
              tips: tips,
              ingreList: ingreList,
              servicePerCont: servicePerCont,
              serviceSizeQty: serviceSizeQty,
              serviceSizeWt: serviceSizeWt,
              shelfLife: shelfLife,
              masterPack: masterPack,
              grossCaseWeight: grossCaseWeight,
              masterUnitSize: masterUnitSize,
              gtinNum: gtinNum,
              caseCount: caseCount,
              calories: calories,
              cholestrol: cholestrol,
              totalFat: totalFat,
              saturatedFat: saturatedFat,
              transFat: transFat,
              sodium: sodium,
              totalCarboht: totalCarboht,
              dietaryFiber: dietaryFiber,
              totalSugar: totalSugar,
              addedSugar: addedSugar,
              protein: protein,
              vitD: vitD,
              calcuim: calcuim,
              iron: iron,
              potassium: potassium,
              cnLabel: cnLabel,
              isActive: isActive
          }
          const productImages = formData.map(data => ({
            imageType: data.imageType,
            image: data.base64Image,
          }));

          const dataToSend = {
            ...payload,
            images: JSON.stringify(productImages)
          };
          axios.post('/product/create', dataToSend, {headers: { Authorization: `Bearer ${localStorage.getItem('token')}` }} )
          .then((r) => {
              setIsSubmitting(false)
              navigate("/products");
          })
          .catch((e) => {
              setIsSubmitting(false)
              toast.error("There is something wrong !!");
          });
        }else{
          toast.error("Please fill all required field");
        }
    }
  return (
    <div className="bg-body-tertiary min-vh-75 d-flex flex-row align-items-center">
      <CContainer>
        <CAlert className='mb-4' color='danger'>Note: All fields are required except GTIN Number, if any field not available please input 0 or NA !!</CAlert>
        <CForm className="row g-3" onSubmit={(e)=>addProductAction(e)}>
        <CRow className="justify-content-center">
          <CCol md={9} lg={7} xl={12}>
            <CCard className="mb-4">
              <CCardHeader>MFG Product Information</CCardHeader>
              <CCardBody className="p-4">
                <CRow className='mb-4'>
                <CCol md={12}>
                  <CFormInput type="text" id="name" label="Name" onChange={(e)=>{setName(e.target.value)}} value={name} name='name' invalid={!!errors.name} />
                </CCol>
                </CRow>

                <CRow className='mb-4'>
                  <CCol md={6}>
                    <CFormInput type="number" id="manuItemNum" label="Manufacturer Item Number" onChange={(e)=>{setManuItemNum(e.target.value)}} value={manuItemNum} name='manuItemNum' invalid={!!errors.manuItemNum} />
                  </CCol>
                  <CCol md={6}>
                    <CFormInput type="text" id="distItemNum" label="Distributor Item Number" onChange={(e)=>{setDistItemNum(e.target.value)}} value={distItemNum} name='distItemNum' invalid={!!errors.distItemNum} />
                  </CCol>
                </CRow>
                <CRow className='mb-4'>
                  <CCol md={12}>
                    <CFormTextarea id="manuDesc" label="Manufacturer Description" onChange={(e)=>{setManuDesc(e.target.value)}} value={manuDesc} name='manuDesc' invalid={!!errors.manuDesc}></CFormTextarea>
                  </CCol>
                </CRow>
                <CRow className='mb-4'>
                  <CCol md={12}>
                    <CFormTextarea id="manuMarkStmt" label="Manufacturer Marketing Statement" onChange={(e)=>{setManuMarkStmt(e.target.value)}} value={manuMarkStmt} name='manuMarkStmt' invalid={!!errors.manuMarkStmt}></CFormTextarea>
                  </CCol>
                </CRow>
                <CRow className='mb-4'>
                <CCol md={6}>
                  <CFormSelect id="distributerId" label="Distributer" name='distributerId' onChange={(e)=>{setDistributerId(e.target.value)}} value={distributerId} invalid={!!errors.distributerId}>
                    <option>...Choose</option>
                    {clients.map((e, key) => {
                        return <option key={key} value={e.id}>{e.companyName}</option>;
                    })}
                  </CFormSelect>
                </CCol>
                <CCol md={6}>
                  <CFormSelect id="productType" label="Product Type" name='productType' onChange={(e)=>{setProductType(e.target.value)}} value={productType} invalid={!!errors.productType}>
                    <option>Choose...</option>
                    <option value='Paper' >Paper</option>
                    <option value='Disposables' >Disposables</option>
                    <option value='Beverages' >Beverages</option>
                    <option value='Food-Frozen' >Food-Frozen</option>
                    <option value='Food-Fresh' >Food-Fresh</option>
                    <option value='DryFoods' >Dry Foods</option>
                    <option value='SmallWares' >Small Wares</option>
                  </CFormSelect>
                </CCol>
                </CRow>
                </CCardBody>
                </CCard>
                </CCol>
                </CRow>
                <CRow>
                  <CCol md={9} lg={7} xl={12}>                    
                    <CCard className="mb-4">
                      <CCardHeader>Product Images</CCardHeader>
                      <CCardBody className="p-4">
                      {formData.map((data, index) => (
                        <CRow key={index} className="mb-3">
                          <CCol>
                            <CFormSelect
                              id={`imageType${index}`}
                              name="imageType"
                              value={data.imageType}
                              onChange={(e) => handleInputChange(index, e)} 
                            >
                              <option value="">...Choose</option>
                              <option value="close">Closed Case</option>
                              <option value="open">Open Case</option>
                              <option value="innerPack">Inner Pack</option>
                              <option value="raw">Raw/Uncooked</option>
                              <option value="plated">Plated</option>
                              <option value="styled">Styled</option>
                            </CFormSelect>
                          </CCol>
                          <CCol>
                            <CFormInput
                              type="file"
                              id={`productImage${index}`}
                              name="logo"
                              onChange={(e) => handleInputChange(index, e)}
                            />
                            {data.logoFile && <CImage rounded thumbnail width={200} height={200} src={data.logoFile} />}
                          </CCol>
                          <CCol>
                            <CButton color="danger" onClick={() => handleRemoveForm(index)}>
                              Remove
                            </CButton>
                          </CCol>
                        </CRow>
                      ))}
                      <CButton type="button" className='btn btn-primary' onClick={handleAddForm}>
                        Add Image
                      </CButton>
                      </CCardBody>
                    </CCard>
                  </CCol>
                </CRow>
                <CRow>
                  <CCol md={9} lg={7} xl={12}>
                    
                    <CCard className="mb-4">
                    <CCardHeader>Product Attributes</CCardHeader>
                  <CCardBody className="p-4">
                    
                <CRow className='mb-4'>
                    <CCol md={6}>
                      <CFormInput type="number" id="gtinNum" label="GTIN Number" onChange={(e)=>{setGtinNum(e.target.value)}} value={gtinNum} name='gtinNum' />
                    </CCol>
                    <CCol md={6}>
                    <CFormInput type='number' locale="en-US" id="caseCount" label="Case Count" onChange={(e)=>{setCaseCount(e.target.value)}} value={caseCount} name='caseCount' invalid={!!errors.caseCount}/>
                    </CCol>
                </CRow>
                  <CRow>
                    <CCol>
                    <CFormInput type="number" id="masterPack" label="Master Pack" onChange={(e)=>{setMasterPack(e.target.value)}} value={masterPack} name='masterPack' invalid={!!errors.masterPack} />
                    </CCol>
                    <CCol>
                    <CFormInput type="number" id="grossCaseWeight" label="Gross Case Weight" onChange={(e)=>{setGrossCaseWeight(e.target.value)}} value={grossCaseWeight} name='grossCaseWeight' invalid={!!errors.grossCaseWeight} />
                    </CCol>
                    <CCol>
                    <CFormInput type="number" id="masterUnitSize" label="Master Unit Size" onChange={(e)=>{setMasterUnitSize(e.target.value)}} value={masterUnitSize} name='masterUnitSize' invalid={!!errors.masterUnitSize} />
                    </CCol>
                  </CRow>
                    <CCol xs={12} className='mb-4'>
                      <CFormInput type='text' id="caseDime" name="caseDime" label="Case Dimensions" placeholder="(LxWxH)" onChange={(e)=>{setCaseDime(e.target.value)}} value={caseDime}  invalid={!!errors.caseDime}/>
                    </CCol>
                    <CCol xs={12} className='mb-4'>
                      <CFormInput type='number' id="servingSize" label="Serving Size" name="servingSize"  onChange={(e)=>{setServingSize(e.target.value)}} value={servingSize} invalid={!!errors.servingSize} />
                    </CCol>
                    <CCol xs={12} className='mb-4'>
                      <CFormInput type='text' id="shelfLifeManu" label="Shelf Life from Manufacturer" name="shelfLifeManu" onChange={(e)=>{setShelfLifeManu(e.target.value)}} value={shelfLifeManu} invalid={!!errors.shelfLifeManu}/>
                    </CCol>
                    <CRow>
                    <CCol md={6}>
                      <CFormSelect id="storageMethod" label="Storage Method" name='storageMethod' onChange={(e)=>{setStorageMethod(e.target.value)}} value={storageMethod} invalid={!!errors.storageMethod}>
                        <option>Choose...</option>
                        <option value='Dry' >Dry</option>
                        <option value='Refrigerated' >Refrigerated</option>
                        <option value='Frozen' >Frozen</option>
                      </CFormSelect>
                    </CCol>
                    <CCol md={6}>
                      <CFormSelect id="shelfLife" label="Shelf Life" name='shelfLife' onChange={(e)=>{setshelfLife(e.target.value)}} value={shelfLife} invalid={!!errors.shelfLife} >
                        <option>Choose...</option>
                        <option value='UseByDate' >Use By Date</option>
                        <option value='days365' >365 days</option>
                        <option value='days365Plus' >365 days plus</option>
                      </CFormSelect>
                    </CCol>
                    </CRow>
                    <CRow className='mb-4'>
                  <CCol md={12}>
                    <CFormTextarea id="ingreList" label="List of all Ingredients" onChange={(e)=>{setIngreList(e.target.value)}} value={ingreList} name='ingreList' invalid={!!errors.ingreList}></CFormTextarea>
                  </CCol>
                </CRow>
                <CRow className='mb-4'>
                  <CCol md={12}>
                    <CFormTextarea id="tips" label="Tips & Handling" onChange={(e)=>{setTips(e.target.value)}} value={tips} name='tips' invalid={!!errors.tips}></CFormTextarea>
                  </CCol>
                </CRow>
                <CRow>
                    <CCol>
                    <CFormInput type="number" id="servicePerCont" label="Servings per Container" onChange={(e)=>{setServicePerCont(e.target.value)}} value={servicePerCont} name='servicePerCont' invalid={!!errors.servicePerCont}/>
                    </CCol>
                    <CCol>
                    <CFormInput type="number" id="serviceSizeQty" label="Serving Size Qty" onChange={(e)=>{setServiceSizeQty(e.target.value)}} value={serviceSizeQty} name='serviceSizeQty' invalid={!!errors.serviceSizeQty} />
                    </CCol>
                    <CCol>
                    <CFormInput type="number" id="serviceSizeWt" label="Serving Size Weight (g)" onChange={(e)=>{setServiceSizeWt(e.target.value)}} value={serviceSizeWt} name='serviceSizeWt' invalid={!!errors.serviceSizeWt} />
                    </CCol>
                  </CRow>
                  </CCardBody>
                </CCard>
                  </CCol>
            </CRow>
            <CRow>
              <CCard>
                <CCardHeader>Nutritional Facts</CCardHeader>
                <CCardBody className="p-4">
                <CRow>
                    <CCol>
                    <CFormInput type="number" id="calories" label="Calories" onChange={(e)=>{setCalories(e.target.value)}} value={calories} name='calories' invalid={!!errors.calories} />
                    </CCol>
                    <CCol>
                    <CFormInput type="number" id="cholestrol" label="Cholesterol (mg)" onChange={(e)=>{setCholestrol(e.target.value)}} value={cholestrol} name='cholestrol' invalid={!!errors.cholestrol}/>
                    </CCol>
                    <CCol>
                    <CFormInput type="number" id="sodium" label="Sodium (mg)" onChange={(e)=>{setSodium(e.target.value)}} value={sodium} name='sodium' invalid={!!errors.sodium} />
                    </CCol>
                  </CRow>
                  <CRow>
                    <CCol>
                    <CFormInput type="number" id="totalFat" label="Total Fat (g)" onChange={(e)=>{setTotalFat(e.target.value)}} value={totalFat} name='totalFat' invalid={!!errors.totalFat} />
                    </CCol>
                    <CCol>
                    <CFormInput type="number" id="saturatedFat" label="Saturated Fat (g)" onChange={(e)=>{setSaturatedFat(e.target.value)}} value={saturatedFat} name='saturatedFat' invalid={!!errors.saturatedFat} />
                    </CCol>
                    <CCol>
                    <CFormInput type="number" id="transFat" label="Trans Fat (g)" onChange={(e)=>{setTransFat(e.target.value)}} value={transFat} name='transFat' invalid={!!errors.transFat} />
                    </CCol>
                  </CRow>
                  <CRow>
                    <CCol>
                    <CFormInput type="number" id="totalCarboht" label="Total Carbohydrate (g)" onChange={(e)=>{setTotalCarboht(e.target.value)}} value={totalCarboht} name='totalCarboht' invalid={!!errors.totalCarboht} />
                    </CCol>
                    <CCol>
                    <CFormInput type="number" id="dietaryFiber" label="Dietary Fiber (g)" onChange={(e)=>{setDietaryFiber(e.target.value)}} value={dietaryFiber} name='dietaryFiber' invalid={!!errors.dietaryFiber} />
                    </CCol>
                  </CRow>
                  <CRow>
                    <CCol>
                    <CFormInput type="number" id="totalSugar" label="Total Sugars (g)" onChange={(e)=>{setTotalSugar(e.target.value)}} value={totalSugar} name='totalSugar' invalid={!!errors.totalSugar} />
                    </CCol>
                    <CCol>
                    <CFormInput type="number" id="addedSugar" label="Added Sugars (g)" onChange={(e)=>{setAddedSugar(e.target.value)}} value={addedSugar} name='addedSugar' invalid={!!errors.addedSugar}/>
                    </CCol>
                  </CRow>
                  <CRow>
                    <CCol>
                    <CFormInput type="number" id="protein" label="Protein (g)" onChange={(e)=>{setProtein(e.target.value)}} value={protein} name='protein' invalid={!!errors.protein} />
                    </CCol>
                    <CCol>
                    <CFormInput type="number" id="vitD" label=" Vitamin D (mcg)" onChange={(e)=>{setVitD(e.target.value)}} value={vitD} name='vitD' invalid={!!errors.vitD} />
                    </CCol>
                  </CRow>
                  <CRow>
                    <CCol>
                    <CFormInput type="number" id="calcuim" label="Calcium (mg)" onChange={(e)=>{setCalcuim(e.target.value)}} value={calcuim} name='calcuim' invalid={!!errors.calcuim} />
                    </CCol>
                    <CCol>
                    <CFormInput type="number" id="iron" label="Iron (mg)" onChange={(e)=>{setIron(e.target.value)}} value={iron} name='iron' invalid={!!errors.iron} />
                    </CCol>
                    <CCol>
                    <CFormInput type="number" id="potassium" label="Potassium (mg)" onChange={(e)=>{setPotassium(e.target.value)}} value={potassium} name='potassium' invalid={!!errors.potassium} />
                    </CCol>
                  </CRow>
              </CCardBody>
              </CCard>
              </CRow>
              <CRow>
                <CCard className="mb-4">
                  <CCardHeader>Published Product</CCardHeader>
                  <CCardBody className="p-4">
                    <CRow className='mb-4'>
                      <CCol>
                        <CFormSelect id="cnLabel" label="C N Label" name='cnLabel' onChange={(e)=>{setCnLabel(e.target.value)}} value={cnLabel}  >
                          <option value='Yes' >Yes</option>
                          <option value='No' >No</option>
                        </CFormSelect>
                      </CCol>
                    </CRow>
                    <CRow className='mb-4'>
                      <CCol>
                        <CFormSelect id="isActive" label="Published Product" name='isActive' onChange={(e)=>{setIsActive(e.target.value)}} value={isActive} >
                          <option value='1' >Published</option>
                          <option value='0' >Unpublished</option>
                        </CFormSelect>
                      </CCol>
                    </CRow>
                  </CCardBody>
                </CCard>
              </CRow>
              <CRow className='p-4'>
              <CCol xs={12}>
                  <CButton color="primary" type="submit" disabled={isSubmitting} className='float-end'>Add Product</CButton>
                </CCol>
              </CRow>
              </CForm>
      </CContainer>
    </div>
  )
}

export default AddProduct
